/* My Rooms Classess */

.myRooms-outer-div{
	margin: 20px 20px;
	padding: 0px 20px;
	height: 100%;
}


.myRooms-header{
	align-items: center;
	text-align: left;
	font-size: 30px;
	display: inline-flex;
	justify-content: space-between;
	width: 100%;
	align-items: center;
}

.myRooms-createNew-button{
	cursor: pointer;
	border: 2px solid var(--secondaryText);
	font-size: 20px;
	background-color: var(--secondaryBackground);
	padding: 10px;
	border-radius: 6px;
	display: flex;
	align-items: center;
}

.myRooms-cards-outer{
	margin: 10px 0px;
	text-align: left;
}


.myRooms-cards-box {
	background-color: var(--secondaryBackground);
	border-radius: 10px;
	box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.3);
  	display: inline-table;
  	min-width: 330px;
	max-width: 400px;
  	margin: 20px;
	cursor: pointer;
	animation: fadeIn 0.5s 1;
	position: relative;
	padding-bottom: 10px;
}


.myRooms-cards-box:hover{
	border-radius: 10px;
	transition: transform .5s;
	transform: scale(1.02);
}

.myRooms-cards-info {
	padding: 3px 5px 10px 5px;
	width: 100%;
}


.myRooms-cards-title {
	font-size: 28px;
	font-weight: bold;
	color: var(--secondaryText);
	letter-spacing: 1px;
	padding: 10px 5px;
	text-align: center;
	/* width: 100%;
	display: inline-flex; */
	align-items: center;
}


.myRooms-cards-description {
	font-size: 18px;
	font-family: 'Muli', sans-serif;
	color: var(--secondaryText);
	padding: 4px 10px;
	letter-spacing: 2px;
	display: flex;
	align-items: center;
}

.myRooms-card-bottom-bar{
	width: 100%;
	padding: 0px 20px;
	margin: auto;
	display: flex;
	justify-content: flex-end;
	align-items: right;
	max-height: 30px;
}

.myRooms-room-visibility{
	border-radius: 20px;
	border: 3px solid var(--hiddenRoom);
	color: var(--hiddenRoom);
	position: relative;
	width: 100px;
	min-width: 100px;
	max-height: 30px;
	text-align: center;
	font-weight: 900;

}


.myRooms-public-room{
	color: var(--publicRoom);
	border: 3px solid var(--publicRoom);
}

.myRooms-private-room{
	color: var(--privateRoom);
	border: 3px solid var(--privateRoom);
}

.myRooms-hidden-room{
	color: var(--hiddenRoom);
	border: 3px solid var(--hiddenRoom);
}


.questions-submitted-div{
	width: 18px;
	height: 18px;
	background: var(--success);
	border-radius:10px;
}
.questions-remaining-div{
	width: 18px;
	height: 18px;
	background: var(--error);
	border-radius:10px;
}

.myRooms-cards-buttons{
	background-color: var(--primaryBackground);
	color: var(--primaryText);
	border-radius: 6px;
	margin-right: 10px;
	font-size: 18px;
	height: 100%;
	padding: 4px 15px;
}


